.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App div {
  user-select: none; /* standard syntax */
  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */
  -khtml-user-select: none; /* webkit (konqueror) browsers */
  -ms-user-select: none; /* IE10+ */
}

#WoofClick{
  height: 40vmin;
  width: 40vmin;
  background-image: url('https://fomodog.club/static/media/fomo.c3a49b0a.png');
  background-size: contain;
  cursor: pointer;
  position:relative;
  margin:auto;
}

#WoofCount{
  font-size:128px;
}

.Woof{
  opacity:1;
  transition: opacity 250ms linear; 
}
.Woof.hide{
  top:0;
  left:50%;
  opacity:0;  
  transition: opacity 250ms linear; 
}
.chooseDog{
  position:fixed;
  width:100%;
  height:100%;
  top:0;
  left:0;
  background-color: #282c34;
  z-index: 10000;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.chooseDogInner{
  max-width:80%;
  max-height:680px;
  margin:auto;
}
.chooseDogFlex{
  display:flex;
  border: 5px solid white;
  padding:20px;
  justify-content: space-around;
}
.chooseDogImg{
  display:flex;
  align-items: center;
}
.chooseDogImg img{
  width: 100%;
  cursor: pointer;
}
